.hero {
  position: relative;
  background-repeat: no-repeat;

  //   background-position: center;
  // background-size: contain;
  background-size: 100% 100%;

  background-color: var(--dark-bg-color);

  text-align: center;
  padding-top: 64px;
  padding-bottom: 30px;
  //   background: linear-gradient(55.91deg, #252b31 19.69%, #7d8286 208.05%);
  background-image: url('../images/hero/hero-bg-320.jpg');
  //   background-image: linear-gradient(55.91deg, #252b31 19.69%, #7d8286 208.05%),
  //     url('./../images/hero/Dust.png'), url('./../images//hero/Snow.png');

  @media screen and (min-device-pixel-ratio: 2) and (min-width: 280px),
    screen and (min-resolution: 192dpi) and (min-width: 280px),
    screen and (min-resolution: 2dppx) and (min-width: 280px) {
    background-image: url('../images/hero/hero-bg-320@2x.jpg');
  }

  @media screen and (min-width: 335px) {
    padding-top: 98px;
  }
  @media screen and (min-width: 525px) {
    padding-top: 119px;
  }
  @media screen and (min-width: 576px) {
    padding-top: 52px;
    padding-bottom: 52px;
    overflow: hidden;

    background-image: url('../images/hero/hero-bg-576.jpg');

    @media screen and (min-device-pixel-ratio: 2) and (min-width: 280px),
      screen and (min-resolution: 192dpi) and (min-width: 280px),
      screen and (min-resolution: 2dppx) and (min-width: 280px) {
      background-image: url('../images/hero/hero-bg-576@2x.jpg');
    }
  }

  @media screen and (min-width: 622px) {
    padding-top: 62px;
  }
  @media screen and (min-width: 638px) {
    padding-top: 80px;
  }

  @media screen and (min-width: 768px) {
    padding-top: 87.5px;
    padding-bottom: 64px;

    background-image: url('../images/hero/hero-bg-768.jpg');

    @media screen and (min-device-pixel-ratio: 2) and (min-width: 280px),
      screen and (min-resolution: 192dpi) and (min-width: 280px),
      screen and (min-resolution: 2dppx) and (min-width: 280px) {
      background-image: url('../images/hero/hero-bg-768@2x.jpg');
    }
  }
  @media screen and (min-width: 1200px) {
    padding-top: 134px;
    padding-bottom: 46px;

    background-image: url('../images/hero/hero-bg-1200.jpg');

    @media screen and (min-device-pixel-ratio: 2) and (min-width: 280px),
      screen and (min-resolution: 192dpi) and (min-width: 280px),
      screen and (min-resolution: 2dppx) and (min-width: 280px) {
      background-image: url('../images/hero/hero-bg-1200@2x.jpg');
    }
  }
  @media screen and (min-width: 1440px) {
    padding-top: 127px;
  }

  @media screen and (min-width: 1680px) {
    padding-top: 203px;
    padding-bottom: 58px;
  }
}

.hero__container {
  &::after {
    // display: block;

    position: absolute;
    content: '';

    background-image: url('../images/hero/hero-img-320.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    @media screen and (min-device-pixel-ratio: 2) and (min-width: 280px),
      screen and (min-resolution: 192dpi) and (min-width: 280px),
      screen and (min-resolution: 2dppx) and (min-width: 280px) {
      background-image: url('../images/hero/hero-img-320@2x.png');
    }

    width: 302px;
    height: 420px;
    // top: 457px;
    left: calc(50% - 300px / 2);
    bottom: 0;

    @media screen and (min-width: 576px) {
      background-image: url('../images/hero/hero-img-576.png');
      @media screen and (min-device-pixel-ratio: 2) and (min-width: 280px),
        screen and (min-resolution: 192dpi) and (min-width: 280px),
        screen and (min-resolution: 2dppx) and (min-width: 280px) {
        background-image: url('../images/hero/hero-img-576@2x.png');
      }
      width: 332px;
      height: 441px;
      left: calc(50% - 278px / 2 + 151px);
    }
    @media screen and (min-width: 768px) {
      // width: 278px;
      // height: 422px;

      background-image: url('../images/hero/hero-img-768.png');
      @media screen and (min-device-pixel-ratio: 2) and (min-width: 280px),
        screen and (min-resolution: 192dpi) and (min-width: 280px),
        screen and (min-resolution: 2dppx) and (min-width: 280px) {
        background-image: url('../images/hero/hero-img-768@2x.png');
      }
      left: calc(50% - 278px / 2);
    }

    @media screen and (min-width: 1200px) {
      background-image: url('../images/hero/hero-img-1200.png');
      @media screen and (min-device-pixel-ratio: 2) and (min-width: 280px),
        screen and (min-resolution: 192dpi) and (min-width: 280px),
        screen and (min-resolution: 2dppx) and (min-width: 280px) {
        background-image: url('../images/hero/hero-img-1200@2x.png');
      }
      width: 472px;
      height: 600px;
      left: calc(50% - 225.5px);
    }
    @media screen and (min-width: 1440px) {
      width: 546px;
      height: 694px;
      left: calc(50% - 546px / 2);
    }

    @media screen and (min-width: 1680px) {
      background-image: url('../images/hero/hero-img-1680.png');
      @media screen and (min-device-pixel-ratio: 2) and (min-width: 280px),
        screen and (min-resolution: 192dpi) and (min-width: 280px),
        screen and (min-resolution: 2dppx) and (min-width: 280px) {
        background-image: url('../images/hero/hero-img-1680@2x.png');
      }
      width: 583px;
      height: 740px;
    }
  }

  @media screen and (min-width: 576px) {
    display: flex;
    // justify-content: space-between;

    // padding-right: 32px;
    // padding-left: 32px;
    // overflow: hidden;
    max-width: 100%;
    padding: 0px 5.48% 0px 32px;
  }

  @media screen and (min-width: 768px) {
    width: 1199px;
    // padding: 0px 8.33% 0px 64px;
    padding-right: 8.33%;
    padding-left: 64px;
    justify-content: space-between;

    // &::before {
    //   content: '';
    //   position: absolute;
    //   background-image: url('../images/hero/hero-quote-img.png');
    //   background-repeat: no-repeat;
    //   // background-position: center;
    //   width: 383px;
    //   height: 117px;
    //   left: calc(50% - 282.94px / 2 + 210.5px);
    //   top: calc(50% - 113.06px / 2 - 74.38px);
    // }
  }

  @media screen and (min-width: 1200px) {
    min-width: 1200px;

    padding-right: 42px;
    padding-left: 42px;
  }

  @media screen and (min-width: 1440px) {
    min-width: 1440px;
  }

  @media screen and (min-width: 1680px) {
    min-width: 1680px;
    padding: 0 70px;
  }
}

.hero-left-wrap {
  @media screen and (min-width: 576px) {
    display: flex;
    flex-direction: column;
    justify-content: start;
    min-width: 244px;
    max-width: 336px;

    margin-right: 24px;
    text-align: left;
  }

  @media screen and (min-width: 768px) {
    max-width: 244px;
    position: relative;
    z-index: 2;
    margin: 0;
  }

  @media screen and (min-width: 1200px) {
    max-width: 381px;
  }
  @media screen and (min-width: 1680px) {
    max-width: 437px;
  }
}

.hero__title {
  margin-bottom: 16px;

  font-family: var(--second-font);
  @include font(400, 31px, 34px);

  color: var(--text-color);

  @media screen and (min-width: 576px) {
    // display: block;
    // min-width: 244px;
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 32px;

    @include font(400, 61px, 67px);
  }
  @media screen and (min-width: 1200px) {
    @include font(400, 76px, 84px);
  }
}

.hero__description {
  margin-bottom: 16px;

  font-family: var(--third-font);
  @include font(400, 16px, 21px);

  color: var(--text-color);

  @media screen and (min-width: 1200px) {
    @include font(400, 20px, 26px);
  }
}

.extra-decription {
  margin-bottom: 64px;
  @media screen and (min-width: 576px) {
    width: 150px;
    margin-bottom: 32px;
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 64px;
    width: 100%;
  }
  @media screen and (min-width: 1440px) {
    margin-bottom: 128px;
  }
}
.hero__buttons-wrapper {
  @media screen and (min-width: 1680px) {
    display: flex;
    justify-content: space-between;
  }
}

.hero__main-button {
  margin-bottom: 16px;

  @media screen and (min-width: 576px) {
    width: 244px;
    margin-left: 0;
  }

  @media screen and (min-width: 1200px) {
    width: 237px;
    padding: 24px 32px 24px 32px;

    @include font(400, 16px, 24px);
  }
  @media screen and (min-width: 1680px) {
    margin-bottom: 0;
  }
}

.hero__book-btn {
  margin-bottom: 280px;

  position: relative;

  @media screen and (min-width: 576px) {
    margin: 0;
    justify-content: start;
  }

