.hero-right-wrap {
  @media screen and (min-width: 576px) {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    min-width: 244px;
    max-width: 324px;
    // justify-content: start;
    // padding: 0;
    // height: 100%;
  }
  @media screen and (min-width: 768px) {
    // min-width: 273px;
    width: 41.2%;
    max-width: none;
  }
  @media screen and (min-width: 1200px) {
    width: fit-content;
  }
  @media screen and (min-width: 1680px) {
    width: 39%;
  }
}

.hero-quote-image {
  // position: absolute;
  display: none;

  @media screen and (min-width: 768px) {
    display: block;
    margin-top: 57px;

    // width: 283px;
    // height: 113px;
    // left: calc(50% - 282.94px / 2 + 210.5px);
    // top: calc(50% - 113.06px / 2 - 74.38px);
