  @media screen and (min-width: 1200px) {
    width: 503px;
    height: 201px;
    margin-top: 52px;
  }
  @media screen and (min-width: 1440px) {
    // width: 503px;
    // height: 201px;
    margin-top: 84.5px;
  }
  @media screen and (min-width: 1680px) {
    margin-top: 46.5px;
  }
}

