.hero__quote {
  font-family: var(--first-font);
  @include font(400, 13px, 20px);
  text-align: justify;

  color: var(--text-color);

  &--bold {
    font-weight: 700;
  }

  &::before {
    position: absolute;
    content: '';
    width: 54px;
    height: 42px;

    background-image: url('../images/hero/quotes.png');
    background-repeat: no-repeat;

    left: 0px;
    top: 8px;

    @media screen and (min-width: 576px) {
      left: 8px;
    }
    @media screen and (min-width: 1200px) {
      // display: block;

      // width: 150px;
      // height: 150px;

      background-image: url('../images/hero/quotes-1200.png');
      background-repeat: no-repeat;
      width: 87px;
      height: 76px;
      // left: -34px;
      // top: -20px;
      left: -60px;
      top: -44px;
    }
  }

  @media screen and (min-width: 1200px) {
    @include font(400, 16px, 24px);
  }
}

